import { Component, Input, SimpleChanges } from '@angular/core';
import {DomSanitizer} from "@angular/platform-browser";
import {ImageviewerComponent} from 'imageviewer/imageviewer.component';

@Component({
    selector: 'patient-image',
    template: require('./patient-image.component.html'),
    styles: [require('./patient-image.component.css')]
})
export class PatientImageComponent {
    @Input() viewerDetail:Object;

    private viewerUrl:any;

    constructor(private domSanitizer : DomSanitizer) {
       // this.name = 'Angular2'
      }

    ngOnChanges(changes: SimpleChanges){

    if(this.viewerDetail){
    this.viewerUrl = this.domSanitizer.bypassSecurityTrustResourceUrl("http://synapsezero.med.fujifilm.info/Viewer/WebQuery?system=datax&userid=ffss&userpassword=ffss2&studyinstanceuid=" + this.viewerDetail.studyUid);

      console.log(this.viewerUrl);

    }

    }

}



