import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';

import {Observable} from 'rxjs/Rx';
import {AbstractService} from '../app.abstract.service';
import {EndPoint} from '../app.endpoints';

// Import RxJs required methods
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';


@Injectable()
export class UserService extends AbstractService{


    constructor (private http: Http, private endPoint:EndPoint) {
        super(http, endPoint);
    }

    
    saveNewUser(data: Object){
        const url = this.endPoint.saveNewUserUrl();
        return this.http.post(url,data)
            // ...and calling .json() on the response to return data
          .map((res:Response) => {
           const location: String = res.headers.get('Location');
	            if(location) {
	                const frag:String[] = location.split("/");
	                return frag[frag.length-1];
	            }
	            console.log("...........saveNewUser.......");
            })
            //...errors if any
            .catch((error:any) => Observable.throw(error || 'Server error'));
    }
	
	getUniqueName(userName:String[]) : Observable<Response> {
    	console.log("dsddsddd",userName);
        const url = this.endPoint.getUniqueUserName() +"?username="+userName+"&email=";
       
        // ...using get request
        return this.http.get(url)
            // ...and calling .json() on the response to return data
            .map((res:Response) => {
            	 console.log("Returnded data",res);
                const json = res.json();
                console.log("Returnded data",json);
                return json;
            })
            // ...errors if any
            .catch((error:any) => Observable.throw(error.json().error || 'Server error'));

    }
    
    getEmailUnique (email:String[]) : Observable<Response> {

        const url = this.endPoint.getUniqueUserName() +"?email="+email+"&username=" ;

        // ...using get request
        return this.http.get(url)
            // ...and calling .json() on the response to return data
            .map((res:Response) => {
                const json = res.json();
                console.log("Returnded data",json);
                return json;
            })
            // ...errors if any
            .catch((error:any) => Observable.throw(error.json().error || 'Server error'));

    }
    
      getUser(userId: String){
        const url = this.endPoint.getUserUrl()+"/"+userId;
        return this.http.get(url)
            // ...and calling .json() on the response to return data
            .map((res:Response) => {
                return res.json();
            })
            //...errors if any
            .catch((error:any) => Observable.throw(error || 'Server error'));

    }
    
    /*update user*/
    updateUser(userId: String, data: Object){
        const url = this.endPoint.saveNewUserUrl() + "/" + userId;
        return this.http.put(url, data)
            // ...and calling .json() on the response to return data
            .map((res:Response) => {
                return res;
            })
            //...errors if any
            .catch((error:any) => Observable.throw(error || 'Server error'));
    }

}



