import { Component, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';
@Component({
    selector: 'option-dropdown-accordion',
    template: require('./worksheet.option.dropdown-accordion.component.html'),
    styles: []
})
export class OptionDropDownAccordion {

@Input() qaId: String;
@Input() qaData: Object;
@Input() qaOptions: Object[] = [];
@Input() qaColumnLayout: number;

ngOnInit(){
    this.initModel();
    if(!this.qaColumnLayout){
        this.qaColumnLayout = 2;
    }
}

initModel(){
    if(!this.qaData){
        this.qaData = {};
    }
}

ngOnChanges(changes: SimpleChanges) {
    if(changes['qaData'])
        this.initModel();
}

onChange(value, id){
    this.qaData[id] = value;
    console.log("qqqqqqqqqqq",this.qaData[id]);
}



}



