import { Component, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';
//import { WorksheetQaData } from '../worksheet.section.quality-assurance.model';
@Component({
    selector: 'worksheet-qa-topic',
    template: require('./worksheet.qa-topic.component.html'),
    styles: []
})
export class QaWorksheetTopic {


    @Input() qaData: Object = {};
    @Input() qaId: String;
    @Input() qaName: String;
    @Input() qaRequired: Boolean;
    @Input() qaType: String ;
    @Input() qaColumnLayout: number;
    @Input() qaOptions: Object[] = [];

    ngOnInit(){
        this.initModel();
    }

    initModel(){
        if(!this.qaData){
            this.qaData = {};
        }
    }

    ngOnChanges(changes: SimpleChanges) {
        if(changes['qaData'])
            this.initModel();
    }



}



