import { Component, Input } from '@angular/core';
import {AppState} from '../app.service.ts';
import {AppComponent} from '../app.component';
@Component({
    selector:'header',
    template: require('./header.component.html'),
    styles: [require('./header.component.css')]
})
export class HeaderComponent { 
	
	 @Input() name:Object;
	
	
	ngAfterViewInit(){


		/* Search  */
		$("#search_bar").click(function(){
			$("#search_bar").hide();
			$("#search_close").show();
			$(".search_box").toggle();
		});
		$("#search_close").click(function(){
			$("#search_close").hide();
			$("#search_bar").show();
			$(".search_box").toggle();
		});
		/* Search Ends */

		/* Mast Head User Dropdown */
		$(".user-dropdown").click(function(){
			$(".user-dropdown ul.dropdown-menu").toggle();
		});
		$('#overlaymenu').click(function(){
			$('ul.mob-dropdown').toggle();
		});
		$('.userDropDownMobile').click(function(){
			$('.userDropDownMobileOptions').slideToggle();
		});
		/* Mast Head User Dropdown Ends */
		
		$("#logout").click(function()
        		{
        			$.ajax({
        				       method : "GET",
        				       url : "/auth/logout",
        				       xhrFields: 
        				       {
        					      withCredentials: true
        				       },
        				       success : function(response)
        				       {
        					      window.location.href = 'login.html';
        				       }
        			      });
        		}); 

	}
}


