import { Component, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';
@Component({
    selector: 'option-dropdown',
    template: require('./worksheet.option.dropdown.component.html'),
    styles: []
})
export class OptionDropDown {

    @Input() qaId: String;
    @Input() qaData: Object;
    @Input() qaOptions: Object[] = [];
    @Input() qaColumnLayout: number;

    ngOnInit(){
        this.initModel();
        if(!this.qaColumnLayout){
            this.qaColumnLayout = 2;
        }

        console.log("ttttttttttttttttttt",this.qaOptions[0].qualityid);
    }

    initModel(){
        if(!this.qaData){
            this.qaData = {};
        }
    }

    ngOnChanges(changes: SimpleChanges) {
        if(changes['qaData'])
            this.initModel();
    }

    onChange(value, id){
        this.qaData[id] = value;
        
    }



}



