    constructor(private observationService: ObservationsService){
    this.autoSaveAlert = true;
    	console.log('this.examTypeChangePopup',this.examTypeChangePopup);
    	console.log('this.autoSave',this.autoSave);
    	this.autoSave.bufferTime(5000)
            .subscribe((val) => {
            	if(val.length){
            		if(!this.worksheetId ){
            			if(this.worksheetCreationEvent){
            				this.autoSave.next();
            				return;
            			}
            		}
            		this.enableLoadingIcon = true;
            		this.saveWorksheet(false,"Procedural").subscribe(res=>{
           				// display auto save complete message
            			if((this.status[0]== "QAAssigned" || this.status[0]== "QAInProgress") && this.workflowStatus== true){
            				this.disableExamApproval = true;
            			}
            			else{
            				this.disableExamApproval = false;
            			}
           				this.enableLoadingIcon = false;
           			},
           			err=>{
           				this.worksheetCreationEvent = null;
           			});
            	}
            });
    }

    initWorksheet(){
    	console.log("vvvvvvvvvvvvvvvvvvvvvvvvvvvvvvvvvvvv",this.assignedUser,this.loginUserId);
    	if(this.assignedUser == this.loginUserId)		
    	{		
        	console.log("when both id's are same",this.assignedUser,this.loginUserId);
        	this.disableExamApproval = false;		
            this.disablePDFButton = true;		
            this.disableWorksheetSelection = false;		
            this.disableWorksheet = false;		
            this.disableSubmitButton = true;	
    	}
    	else {
    		console.log("inside else condition",this.assignedUser,this.loginUserId);
    		this.disableExamApproval = true;		
            this.disablePDFButton = true;		
            this.disableWorksheetSelection = true;		
            this.disableWorksheet = true;		
            this.disableSubmitButton = true;	
    	}
        
    	
        if(this.examType && this.examType.length > 0 && this.studyId){
        	console.log("inside examType condition",this.assignedUser,this.loginUserId);
            this.selectedExamType = this.examType[0][0]['examtype'];
            this.selectedExamTypeId = this.examType[0][0]['id'];
            this.loadWorksheetList();
            this.loadWorksheet();
            this.saveToSubmit();
        }
    }
    
